<template>
   <transition name="fade">
 <div v-show="visible" class="dialog-mask">
   <div  class="dialog">
     <div v-if="header" class="header">
       <span class="title">{{title}}</span>
       <img class="close-btn" :src="closeBtn" alt="" @click="handleClose">
     </div>
     <div class="container">
       <slot></slot>
     </div>
     <div v-if="footer" class="footer">
       <img class="confirm-btn" :src="btnBg" alt="" @click="handleConfirm">
     </div>
   </div>
 </div>
 </transition>
</template>

<script>

export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    header: {
      type: Boolean,
      defaule: true
    },
    title: {
      type: String
    },
    confirm: {
      type: String,
      default: '知道了'
    },
    footer: {
      type: Boolean,
      default: true
    },
    btnBg: {
      type: String
    }
  },
 data() {
   return {
     closeBtn: require('@/assets/game/close_img.png')
   }
 },

 methods: {
   // 關閉
   handleClose() {
     this.$emit('close')
   },
   // 確定
   handleConfirm() {
     this.$emit('confirm')
   }
 }
}
</script>

<style lang="less" scoped>
.dialog-mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, .6);
}
.dialog {
  width: 62vw;
  min-height: 36vh;
  background: #FFF9EB;
  border-radius: 8px;
  border: 1px solid #D1B171;
  .header {
    width: 100%;
    height: 40px;
    position: relative;
  }
  .close-btn {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 12px;
    top: 12px;
  }
}

.footer {
  text-align: center;
  .confirm-btn {
    display: inline-block;
    width: 25.6vw;
    height: 4.4vh;
    background-size: 100% 100%;
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>