<template>
  <div class="game-wrap">
    <div class="title-row">
      <img class="game-title"
           :src="title"
           alt="">
    </div>
    <div class="question">
      <div class="top">
        <img style="width: 100%;padding-top: 80px;" :src="answer" alt="">
      </div>
      <div class="difficulty">
        <span>难度：</span>
        <span v-for="item  in difficultyData"
              :key="item.value"
              :class="currDiff === item.value ? 'difficulty-item active' : 'difficulty-item'"
              @click="currDiff = item.value">{{item.label}}</span>
      </div>
      <div class="options">
        <div class="tips">
          <span>请选择</span>
          <span @click="handleExplain">
            <SvgIcon icon-class="explain"
                   class="explain" />
            游戏说明
          </span>
        </div>
        <div class="answer">
          <span v-for="item in answerData"
                :key="item.value"
                class="answer-item">{{item.label}}</span>
        </div>
        <div class="operation">
          <img class="publish-btn"
               :src="publish"
               alt=""
               @click="handleConfirmAnswer">
          <img class="reset-btn"
               :src="reset"
               alt="">
        </div>
      </div>
      <div class="back-row">
        <span @click="handleBack">
          <SvgIcon icon-class="back-btn"
                   class="back-icon" />
          <em class="back-text">返回</em>
        </span>
      </div>
      <!-- 答題結果 -->
      <Dialog :visible="visible" :header="true" :btnBg="btnBg" @close="handleClose" @confirm="handleConfirm">
        <div class="dialog-container">
          <img style="width: 16vw" :src="correct" alt="">
          <p class="answer-text">{{answerText}}</p>
        </div>
      </Dialog>
      <!-- 遊戲說明 -->
      <Dialog :visible="explainVisible" :header="false" :btnBg="confirmBg" @close="handleCloseExplain" @confirm="handleConfirmExplain">
        <div class="dialog-container">
          <div class="explain-wrap">
            <img class="explain-img" :src="explain" alt="">
          </div>
        </div>
      </Dialog>
    </div>
  </div>
</template>

<script>
import { SvgIcon, Dialog } from '@/components'

export default {
  components: {
    SvgIcon,
    Dialog
  },
  data() {
    return {
      title: require('@/assets/game/proverb_game_title.png'),
      publish: require('@/assets/game/publish.png'),
      reset: require('@/assets/game/reset.png'),
      confirmBg: require('@/assets/game/confirm_btn.png'),
      conturnBg: require('@/assets/game/conturn.png'),
      resetBg: require('@/assets/game/reset_btn.png'),
      correct: require('@/assets/game/correct.png'),
      error: require('@/assets/game/error.png'),
      explain: require('@/assets/game/explain.png'),
      answer: require('@/assets/game/answer-item.png'),
      btnBg: '',
      answerResultImg: '',
      difficultyData: [
        { label: '简单', value: 0 },
        { label: '中等', value: 1 },
        { label: '难', value: 2 },
      ],
      answerData: [
        { label: '豪', value: 0 },
        { label: '牛', value: 1 },
        { label: '走', value: 2 },
        { label: '不', value: 3 },
        { label: '豪', value: 4 },
      ],
      currDiff: 0,
      visible: false,
      answerText: '恭喜您答对了',
      explainVisible: false
    }
  },
  methods: {
    // 查看說明
    handleExplain() {
      this.explainVisible = true
    },
    // 關閉說明
    handleCloseExplain() {
      this.explainVisible = false
    },
    // 知道了
    handleConfirmExplain() {
      this.explainVisible = false
    },
    // 關閉
    handleClose() {
      this.visible = false
      this.btnBg = ''
      this.answerResultImg = ''
      this.answerText = ''
    },
    // 確定
    handleConfirm() {
      this.visible = false
    },
    // 點擊完成
    handleConfirmAnswer() {
      this.answerResultImg = this.confirmBg
      this.answerText = '恭喜您答对了'
      this.btnBg = this.conturnBg
      this.visible = true
    },
    // 返回
    handleBack() {
      this.$router.back()
    }
  }
}
</script>

